<h2 id="cross-site-scripting-xss" tabindex="-1"><a class="header-anchor" href="#cross-site-scripting-xss" aria-hidden="true">#</a> Cross-Site Scripting (XSS)</h2>
<h3 id="vulnerability" tabindex="-1"><a class="header-anchor" href="#vulnerability" aria-hidden="true">#</a> Vulnerability</h3>
<p>The vulnerability Cross-Site Scripting (XSS) is when a hacker manage to inject client-side code (HTML, CSS or JavaScript, etc.) on your website that is executed by your users' web browsers when they visit your website. By injecting client-side JavaScript code a hacker can tell the users' web browsers to do some bad things for the user, such as:</p>
<ul>
<li>Read information found on the webpage (which might be sensitive information about the user if she has logged in to an account) and then send that information to the hacker's own server in an HTTP request.</li>
<li>Send HTTP requests to various websites. These websites will think the requests are intentionally sent by the user, so they will happily carry out the requests. This is especially troublesome if the user has logged in to an account on these websites.</li>
</ul>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>In the search form below users should be able to enter search terms, and then get back a list with the search result. To indicate to the user what she has searched for, it also displays the search term. In this example, the search result is never displayed, only what the user searched for. Try entering a search term containing HTML code, for example <code>&lt;b&gt;Hi!&lt;/b&gt;</code>, or <code>&lt;b onclick=&quot;alert('Hi!')&quot;&gt;Click me!&lt;/b&gt;</code> (client-side JavaScript must be enabled in your web browser for the example to work). Although these examples are not harmful to the user, it shows the potential danger with the vulnerability if a hacker takes advantage of it.</p>
<form onsubmit="event.preventDefault(); document.getElementById('rrr').innerHTML = 'You searched for: '+document.getElementById('sss').value">
    <p>
        Search:
        <input id="sss">
        <input type="submit" value="Go">
    </p>
</form>
<p id="rrr"></p>
</div>
<h3 id="protection" tabindex="-1"><a class="header-anchor" href="#protection" aria-hidden="true">#</a> Protection</h3>
<p>When data comes from an untrusted source (for example a user or a third-party application you are using) you must escape the HTML code before sending it to the web browser as part of the webpage in the HTTP response. You can escape the data by replacing the following characters in it (the characters that have special meaning in HTML syntax) with their corresponding entity names:</p>
<ul>
<li><code>&lt;</code> can be replaced with <code>&amp;lt;</code>.</li>
<li><code>&gt;</code> can be replaced with <code>&amp;gt;</code>.</li>
<li><code>&quot;</code> can be replaced with <code>&amp;quot;</code>.</li>
<li><code>'</code> can be replaced with <code>&amp;apos;</code>.</li>
</ul>
<p>When the web browser for example sees <code>&amp;lt;</code>, it will display the <code>&lt;</code> character.</p>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>With proper protection, the search form in the previous example would work like the one shown below (client-side JavaScript must be enabled in your web browser for the example to work).</p>
<form onsubmit="event.preventDefault(); document.getElementById('rrrr').innerText = 'You searched for: '+document.getElementById('ssss').value">
    <p>
        Search:
        <input id="ssss">
        <input type="submit" value="Go">
    </p>
</form>
<p id="rrrr"></p>
</div>
<CodeGroup>
<CodeGroupItem title="Node.js/Express">
<p v-pre>
When you use `request.send("Input from a client.")` you are responsible for escaping the input yourself. When you pass input to a view, the view engine usually escapes the data for you. For example, when using Handlebars as your view engine, data you insert into the view using `{{expression}}` will escape the HTML code in `expression`. This is usually what you want to happen, but if you for some reason don't want that, you can use `{{{expression}}}`, which won't escape the HTML code in `expression`, but then you are responsible to make sure that no XSS vulnerability exists.
</p>
</CodeGroupItem>
<CodeGroupItem title="PHP">
<p>PHP has a function called <a href="https://www.php.net/manual/en/function.htmlspecialchars.php" target="_blank" rel="noopener noreferrer">htmlspecialchars()<OutboundLink/></a> you can use to escape HTML code.</p>
</CodeGroupItem>
</CodeGroup>
<h2 id="cross-site-request-forgery-csrf" tabindex="-1"><a class="header-anchor" href="#cross-site-request-forgery-csrf" aria-hidden="true">#</a> Cross-Site Request Forgery (CSRF)</h2>
<h3 id="vulnerability-1" tabindex="-1"><a class="header-anchor" href="#vulnerability-1" aria-hidden="true">#</a> Vulnerability</h3>
<p>The vulnerability Cross-Site Request Forgery (CSRF) is about tricking the user's web browser into sending HTTP requests to other websites that is gainable for the hacker. This is especially troublesome if the user is logged in to an account on these websites, for example:</p>
<ol>
<li>Alice sends her correct login credentials to bank.com.</li>
<li>bank.com creates a session containing an identifier for Alice's bank account.</li>
<li>Alice web browser stores the session's identifier in a cookie.</li>
<li>Alice somehow ends up at bad-website.com, which contains client-side code from a hacker.</li>
<li>The bad client-side code tells Alice's web browser to send an HTTP request to bank.com to transfer $1000 to the hacker's own bank account. Since Alice before logged in to bank.com, that cookie with the session identifier is attached to the request, so to bank.com it looks like the HTTP request is intentionally sent by Alice, so bank.com carries out the request.</li>
</ol>
<h2 id="sql-injection" tabindex="-1"><a class="header-anchor" href="#sql-injection" aria-hidden="true">#</a> SQL injection</h2>
<h3 id="vulnerability-2" tabindex="-1"><a class="header-anchor" href="#vulnerability-2" aria-hidden="true">#</a> Vulnerability</h3>
<p>The vulnerability SQL injection is when a hacker manages to alter an SQL query the web application sends to the database. This happen when the web application tries to dynamically build the SQL query with some values it has received from the client, and the hacker can alter the query by providing a carefully chosen value.</p>
<p>Imagine a website containing user accounts. The URI <code>/accounts/ACCOUNT_ID</code> is used to identify the account with the id <code>ACCOUNT_ID</code>. For example, <code>/accounts/3</code> identifies the account with the id <code>3</code>. When a GET request for this URI is received, the web application needs to send a query like <code>SELECT * FROM accounts WHERE id = ACCOUNT_ID</code> to the database. If that query is dynamically generated like this:</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code>app<span class="token punctuation">.</span><span class="token function">get</span><span class="token punctuation">(</span><span class="token string">"/accounts/:ACCOUNT_ID"</span><span class="token punctuation">,</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter">request<span class="token punctuation">,</span> response</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    
    <span class="token keyword">const</span> <span class="token constant">ACCOUNT_ID</span> <span class="token operator">=</span> request<span class="token punctuation">.</span>params<span class="token punctuation">.</span><span class="token constant">ACCOUNT_ID</span>
    
    <span class="token keyword">const</span> query <span class="token operator">=</span> <span class="token string">"SELECT * FROM accounts WHERE id = "</span><span class="token operator">+</span><span class="token constant">ACCOUNT_ID</span>
    
    <span class="token comment">// Send query to DB.</span>
    
<span class="token punctuation">}</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br></div></div><p>Then the hacker can use a clever <code>ACCOUNT_ID</code> to alter the query sent to the database. If an expected account id like <code>3</code> is used, then the query sent to the database will be <code>SELECT * FROM accounts WHERE id = 3</code>, but if the hacker uses an account id like <code>-1 OR username = 'Alice'</code>, then the query sent to the database will be <code>SELECT * FROM accounts WHERE id = -1 OR username = 'Alice'</code>, which the hacker can use to to retrieve Alice's account. This might not be a big deal, but if the hacker would use the account id <code>-1 OR password = 'abc123'</code>, he would get back the first user to have the password <code>abc123</code>, and can then simply login to that account (assuming passwords aren't hashed).</p>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>Below you can change the URI and see how the query to the database changes (requires client-side JavaScript to be enabled).</p>
<p><code>GET /accounts/<input oninput="document.getElementById('ppp').innerText = this.value"></code></p>
<p><code>SELECT * FROM accounts WHERE id = <span id="ppp"></span></code></p>
</div>
<h3 id="protection-1" tabindex="-1"><a class="header-anchor" href="#protection-1" aria-hidden="true">#</a> Protection</h3>
<p>Don't use input from the client (a query string parameter, a cookie, the body of the request, a dynamic URI parameter, etc.) to dynamically generate SQL queries sent to the database. Instead, use placeholders for dynamic values in the query, and pass the values separately to the database.</p>
<CodeGroup>
<CodeGroupItem title="Node.js/Express/SQLite 3">
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code>app<span class="token punctuation">.</span><span class="token function">get</span><span class="token punctuation">(</span><span class="token string">"/accounts/:ACCOUNT_ID"</span><span class="token punctuation">,</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter">request<span class="token punctuation">,</span> response</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    
    <span class="token keyword">const</span> <span class="token constant">ACCOUNT_ID</span> <span class="token operator">=</span> request<span class="token punctuation">.</span>params<span class="token punctuation">.</span><span class="token constant">ACCOUNT_ID</span>
    
    <span class="token keyword">const</span> query <span class="token operator">=</span> <span class="token string">"SELECT * FROM accounts WHERE id = ?"</span>
    <span class="token keyword">const</span> values <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token constant">ACCOUNT_ID</span><span class="token punctuation">]</span>
    
    <span class="token comment">// Send query and values to DB.</span>
    
<span class="token punctuation">}</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br></div></div></CodeGroupItem>
<CodeGroupItem title="PHP">
<p>Using the old (now deprecated) <code>mysql_***()</code> functions, you needed to escape untrusted values yourself by calling the function <a href="https://www.php.net/manual/en/function.mysql-real-escape-string.php" target="_blank" rel="noopener noreferrer">mysql_real_escape_string()<OutboundLink/></a>:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token function">mysql_connect</span><span class="token punctuation">(</span><span class="token comment">/* Connection settings... */</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token variable">$ACCOUNT_ID</span> <span class="token operator">=</span> <span class="token variable">$_GET</span><span class="token punctuation">[</span><span class="token string single-quoted-string">'ACCOUNT_ID'</span><span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token function">mysql_query</span><span class="token punctuation">(</span><span class="token string double-quoted-string">"SELECT * FROM accounts WHERE id = "</span><span class="token operator">.</span><span class="token function">mysql_real_escape_string</span><span class="token punctuation">(</span><span class="token variable">$ACCOUNT_ID</span><span class="token punctuation">)</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div><p>Many programmers often forgot to call this functions, leading to that many SQL injection vulnerabilities existed. To prevent programmers from forgetting this, the <code>mysql_***()</code> functions were deprecated, and the <code>mysqli_***()</code> functions and PDO were introduced instead. Using the newer <code>mysqli_***()</code> functions you use placeholders in prepared statements like this:</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$connection</span> <span class="token operator">=</span> <span class="token function">mysqli_connect</span><span class="token punctuation">(</span><span class="token comment">/* Connection settings... */</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token variable">$ACCOUNT_ID</span> <span class="token operator">=</span> <span class="token variable">$_GET</span><span class="token punctuation">[</span><span class="token string single-quoted-string">'ACCOUNT_ID'</span><span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$statement</span> <span class="token operator">=</span> <span class="token function">mysqli_prepare</span><span class="token punctuation">(</span><span class="token variable">$connection</span><span class="token punctuation">,</span> <span class="token string double-quoted-string">"SELECT * FROM accounts WHERE id = ?"</span><span class="token punctuation">)</span>

<span class="token function">mysqli_stmt_bind_param</span><span class="token punctuation">(</span><span class="token variable">$statement</span><span class="token punctuation">,</span> <span class="token string double-quoted-string">"i"</span><span class="token punctuation">,</span> <span class="token variable">$ACCOUNT_ID</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token function">mysqli_stmt_execute</span><span class="token punctuation">(</span><span class="token variable">$statement</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token function">mysqli_stmt_bind_result</span><span class="token punctuation">(</span><span class="token variable">$statement</span><span class="token punctuation">,</span> <span class="token variable">$result</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token function">mysqli_stmt_fetch</span><span class="token punctuation">(</span><span class="token variable">$statement</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br></div></div></CodeGroupItem>
</CodeGroup>
